@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;600;700&display=swap");
header {
  background-color: rgba(var(--accent-rgb), 0.8);
  backdrop-filter: blur(5px);
  padding: 1rem;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  transition: all 0.3s ease;
}
header:hover {
  background-color: rgba(var(--accent-rgb), 1);
}

.logo {
  color: white;
  font-weight: bold;
  font-size: 1.2rem;
  text-decoration: none;
}

nav {
  padding-inline: 2rem;
}
nav ul {
  list-style-type: none;
  padding: 0;
  display: flex;
  margin: 0;
}
nav ul li {
  margin: 0 1rem;
}
nav ul li a {
  color: white;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s ease;
  position: relative;
}
nav ul li a::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: -5px;
  left: 0;
  background-color: white;
  transform: scaleX(0);
  transition: transform 0.3s ease;
}
nav ul li a:hover::after {
  transform: scaleX(1);
}
nav ul li a #github-icon {
  height: 20px;
  margin-left: 7px;
  margin-top: 3px;
}

.hamburger {
  display: none;
  flex-direction: column;
  cursor: pointer;
}

.hamburger span {
  width: 25px;
  height: 3px;
  background-color: white;
  margin: 2px 0;
  transition: 0.4s;
}

main {
  margin-top: 4rem;
  position: relative;
  z-index: 10;
}
main section {
  margin-bottom: 6rem;
  padding-bottom: 2rem;
  scroll-margin-top: 5rem;
}
main .content section {
  opacity: 0;
  transform: translateY(20px);
  animation: fadeInUp 1s ease-out forwards;
}

h1,
h2,
h3 {
  color: var(--txt);
  font-weight: 700;
}

h2 {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  background: linear-gradient(135deg, var(--accent), var(--secondary));
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  display: inline-block;
}

p {
  color: var(--txt);
}

.hero {
  min-height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
  padding: 0 1rem;
}
.hero article {
  max-width: 800px;
  z-index: 2;
  animation: heroReveal 1.5s cubic-bezier(0.2, 0.8, 0.2, 1);
}
.hero h1 {
  font-size: 4.5rem;
  line-height: 1.1;
  margin-bottom: 1.5rem;
  letter-spacing: -0.02em;
  background: radial-gradient(circle at center, var(--txt) 0%, var(--txt-muted) 100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 0 30px rgba(var(--accent-rgb), 0.3);
}
.hero p {
  font-size: 1.5rem;
  margin-bottom: 3rem;
  color: var(--secondary);
  font-weight: 300;
}

.content {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 1.5rem;
}

.cta-button {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background: linear-gradient(135deg, var(--accent), #502ec2);
  color: white;
  padding: 0.8rem 2rem;
  border-radius: 50px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid rgba(255, 255, 255, 0.1);
  position: relative;
  overflow: hidden;
  z-index: 1;
}
.cta-button:hover {
  transform: translateY(-3px) scale(1.02);
  box-shadow: 0 10px 30px -10px rgba(var(--accent-rgb), 0.6);
  border-color: rgba(255, 255, 255, 0.3);
}
.cta-button:hover::after {
  left: 100%;
}
.cta-button::after {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s ease;
  z-index: -1;
}

#projects {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.project {
  background-color: var(--card-bg);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 1rem;
  padding: 2rem;
  transition: all 0.4s ease;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.project:hover {
  transform: translateY(-10px);
  border-color: rgba(var(--accent-rgb), 0.3);
  box-shadow: 0 20px 40px -20px rgba(0, 0, 0, 0.7);
}
.project:hover h3 {
  color: var(--secondary);
}
.project h3 {
  color: var(--accent);
  font-size: 1.5rem;
  margin-bottom: 1rem;
  transition: color 0.3s ease;
}
.project p {
  margin-bottom: 1.5rem;
  flex-grow: 1;
}
.project ul {
  padding-left: 1.2rem;
  margin-bottom: 1.5rem;
  color: var(--txt-muted);
}
.project ul li {
  margin-bottom: 0.5rem;
}
.project .sub-projects {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}
.project .sub-projects li {
  margin: 0;
}
.project .sub-projects li a {
  font-size: 0.9rem;
  color: var(--txt);
  background: rgba(255, 255, 255, 0.05);
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  text-decoration: none;
  transition: all 0.2s;
}
.project .sub-projects li a:hover {
  background: var(--accent);
  color: white;
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.skill-tag {
  background: rgba(var(--accent-rgb), 0.1);
  color: var(--secondary);
  border: 1px solid rgba(var(--accent-rgb), 0.2);
  padding: 0.6rem 1.2rem;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 500;
  transition: all 0.3s ease;
  cursor: default;
}
.skill-tag:hover {
  background: var(--accent);
  color: white;
  transform: scale(1.05) rotate(2deg);
  box-shadow: 0 5px 15px rgba(var(--accent-rgb), 0.4);
}

.social-links {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  gap: 1.5rem;
}
.social-links .social-icon {
  color: var(--txt-muted);
  width: 30px;
  height: 30px;
  transition: all 0.3s;
}
.social-links .social-icon:hover {
  color: var(--secondary);
  transform: translateY(-3px);
}
.social-links .social-icon svg {
  width: 100%;
  height: 100%;
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes heroReveal {
  0% {
    opacity: 0;
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
@media screen and (max-width: 768px) {
  .hamburger {
    display: flex;
  }
  nav {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background-color: rgba(var(--accent-rgb), 0.9);
    backdrop-filter: blur(10px);
    display: none;
    padding: 1rem 0;
  }
  nav.active {
    display: block;
  }
  nav ul {
    flex-direction: column;
    align-items: center;
  }
  nav ul li {
    margin: 0.5rem 0;
  }
  .hero h1 {
    font-size: 2rem;
  }
  .hero article {
    width: 90%;
  }
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeInLeft {
  0% {
    opacity: 0.6;
    transform: translateX(-120%) scaleY(0.95);
  }
  70% {
    opacity: 0.9;
    transform: translateX(3%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
#flying-object {
  position: fixed;
  width: 20px;
  height: 20px;
  background-color: rgba(var(--secondary-rgb), 0.5);
  border-radius: 50%;
  pointer-events: none;
  transition: transform 0.1s ease, background-color 0.3s ease, width 0.1s, height 0.1s;
  z-index: 9999;
  mix-blend-mode: screen;
  box-shadow: 0 0 10px var(--secondary);
}
#flying-object.fo-bg {
  background-color: var(--accent);
  box-shadow: 0 0 20px var(--accent);
}

:root {
  --accent: #7c4dff;
  --accent-rgb: 124, 77, 255;
  --secondary: #00e5ff;
  --secondary-rgb: 0, 229, 255;
  --back: #0a0a0c;
  --back-rgb: 10, 10, 12;
  --card-bg: #151518;
  --txt: #ececf1;
  --txt-muted: #a1a1aa;
}

body {
  font-family: "Outfit", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: var(--back);
  color: var(--txt);
  line-height: 1.6;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

footer {
  text-align: center;
  padding: 0.5rem;
  background-color: var(--accent);
  color: white;
  font-size: 0.8rem;
}